.mysubjects-div-backgroud{
    padding: 24px;
    background: #ccc;
    display: flex;
    align-items: center;
    justify-content: center;
}

.mysubjects-div-main {
    width: 100%;
    max-width: 1100px;
}
.mysubjects-div-menu {
    width: 100%;
    display: flex;    
    overflow-x: scroll;
    justify-content: flex-start;
    align-content: flex-start;
    align-items: center;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    background: rgb(0,21,41);
    margin-top: 30px;
}

.mysubjects-div-menu .menu-item {
    display: inline-block;
    text-align: center;
    font-size: 12px;
    color: rgba(255,255,255);
    padding: 10px 10px;
    position: relative;
    opacity: 0.4;
}
.mysubjects-div-menu .menu-item:hover{
    opacity: 1;
}
.mysubjects-div-menu .menu-img{
    width: 60px;
    padding: 5px;
}
.mysubjects-div-content{
    width: 100%;
    height: 100%;
    min-height: 300px;
    padding: 24px;    
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    background:#f5f5f5;
    margin-bottom: 30px;
}